import * as React from "react";
import { IMessage } from "../interfaces/message";
import {
  Card,
  Button,
  Input,
  Form,
  Select,
  InputNumber,
  Radio,
  Typography,
  Checkbox,
  Row,
  Col,
  Upload,
} from "antd";
import { MessageType, ResponseType } from "../enums/message";

type Props = {
  message: IMessage;
  previousMessage: IMessage | undefined;
  highlighted: string | undefined;
  setHighlighted: (id: string | undefined) => void;
};

const MessageCard: React.FC<Props> = ({
  message,
  previousMessage,
  highlighted,
  setHighlighted,
}: Props) => {
  const [form] = Form.useForm();

  const [responseType, setResponseType] = React.useState<string>(
    message.response_type || ResponseType.TEXT
  );
  const [messageType, setMessageType] = React.useState<string>(
    message.type || MessageType.TEXT
  );

  return (
    <Card
      hoverable={true}
      style={{ width: 256, cursor: "default", marginBottom: 16 }}
      // onMouseEnter={() => setHighlighted(message.previous_message_id)}
      // onMouseLeave={() => setHighlighted(undefined)}
      // bodyStyle={{
      //   borderTop:
      //     highlighted === message._id ? "solid 1px #1890FF" : "solid 1px white",
      //   transition: "0.3s",
      // }}
    >
      <Typography.Paragraph>
        ID:{" "}
        <Typography.Text ellipsis={true} copyable>
          {message._id}
        </Typography.Text>
      </Typography.Paragraph>

      {/* previous message */}
      <Typography.Paragraph ellipsis={false}>
        From: {previousMessage?.title || "None"}{" "}
        {previousMessage?.response_type === ResponseType.QUICK_REPLY &&
          previousMessage?.quick_replies &&
          previousMessage?.quick_replies[0] &&
          `(${previousMessage?.quick_replies[0].text})`}
      </Typography.Paragraph>

      <Form
        form={form}
        size="small"
        colon={false}
        initialValues={{
          title: message.title,
          type: message.type,
          response_type: message.response_type,
          text: message.text,
          quick_replies: message.quick_replies,
          delay: message.delay,
          use_as_quote: message.use_as_quote,
          use_as_title: message.use_as_title,
          use_in_todo: message.use_as_todo,
          use_in_review: message.use_in_review,
        }}
        onFieldsChange={(changedFields, allFields) => {
          const responseTypeChange = changedFields.find(
            (field) => field.name.toString() === "response_type"
          );

          if (responseTypeChange) {
            setResponseType(responseTypeChange.value);
            return;
          }

          // If message type is changed set state
          const messageTypeChange = changedFields.find(
            (field) => field.name.toString() === "type"
          );

          if (messageTypeChange) {
            setMessageType(messageTypeChange.value);
            return;
          }
        }}
      >
        {/* title */}
        <Form.Item label="Title" name="title">
          <Input placeholder="Enter message title" />
        </Form.Item>

        {/* select message type */}
        <Form.Item label="Message type" name="type">
          <Select>
            <Select.Option value={MessageType.TEXT}>Text</Select.Option>
            <Select.Option value={MessageType.IMAGE}>Image</Select.Option>
            <Select.Option value={MessageType.VIDEO}>Video</Select.Option>
            <Select.Option value={MessageType.AUDIO}>Audio</Select.Option>
          </Select>
        </Form.Item>

        {messageType === MessageType.TEXT ? (
          /* message text */
          <Form.Item label="Message text" name="text" labelCol={{ span: 24 }}>
            <Input.TextArea
              placeholder="Enter message text"
              autoSize={{ minRows: 2, maxRows: 6 }}
            />
          </Form.Item>
        ) : (
          /* file input */
          <Form.Item
            label="File"
            name="text"
            labelCol={{ span: 24 }}
            tooltip="File must be less than 5mb"
          >
            <Upload
              fileList={[]}
              listType="text"
              showUploadList={false}
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
            >
              <Button>Upload</Button>
            </Upload>
          </Form.Item>
        )}

        {/* delay */}
        <Form.Item label="Delay" name="delay">
          <InputNumber
            placeholder="Enter delay in seconds"
            type="number"
            min={0}
            max={60}
            addonAfter="seconds"
          />
        </Form.Item>

        {/* response type */}
        <Form.Item
          label="Response type"
          name="response_type"
          labelCol={{ span: 24 }}
        >
          <Radio.Group buttonStyle="outline">
            <Radio.Button value={ResponseType.TEXT}>Text</Radio.Button>
            <Radio.Button value={ResponseType.QUICK_REPLY}>
              Quick reply
            </Radio.Button>
            <Radio.Button value={ResponseType.NONE}>None</Radio.Button>
          </Radio.Group>
        </Form.Item>

        {/* dynamicaly add buttons for quick reply options */}
        {responseType === ResponseType.QUICK_REPLY && (
          <>
            <Form.Item
              name="branch_after"
              valuePropName="checked"
              style={{ marginBottom: 0 }}
            >
              <Checkbox>Branch after</Checkbox>
            </Form.Item>
            <Form.Item
              label="Quick reply options"
              labelCol={{ span: 24 }}
              noStyle
            >
              <Form.List name="quick_replies">
                {(fields, { add, remove }) => (
                  <div>
                    {fields.map((field, index) => (
                      <Form.Item
                        {...field}
                        key={field.key}
                        required={false}
                        fieldKey={index}
                        name={[field.name, "text"]}
                        style={{ marginBottom: 8 }}
                      >
                        <Row>
                          <Col span={16}>
                            <Input placeholder="Enter quick reply option" />
                          </Col>
                          <Col span={8}>
                            <Button
                              danger
                              style={{ height: 16 }}
                              type="link"
                              size="small"
                              onClick={() => remove(field.name)}
                            >
                              Remove
                            </Button>
                          </Col>
                        </Row>
                      </Form.Item>
                    ))}
                    <Form.Item>
                      <Button type="dashed" onClick={() => add()} block>
                        Add quick reply option
                      </Button>
                    </Form.Item>
                  </div>
                )}
              </Form.List>
            </Form.Item>
          </>
        )}

        {/* Additional checkboxes */}
        <Form.Item labelCol={{ span: 24 }} label="Additional options">
          <Form.Item
            name="use_as_quote"
            valuePropName="checked"
            style={{ marginBottom: 0 }}
          >
            <Checkbox>Use as quote</Checkbox>
          </Form.Item>
          <Form.Item
            name="use_as_title"
            valuePropName="checked"
            style={{ marginBottom: 0 }}
          >
            <Checkbox>Use as title</Checkbox>
          </Form.Item>
          <Form.Item
            name="use_as_todo"
            valuePropName="checked"
            style={{ marginBottom: 0 }}
          >
            <Checkbox>Use in To Do list</Checkbox>
          </Form.Item>
          <Form.Item
            name="use_in_review"
            valuePropName="checked"
            style={{ marginBottom: 0 }}
          >
            <Checkbox>Use in Review</Checkbox>
          </Form.Item>
          <Form.Item name="review_title" style={{ marginBottom: 0 }}>
            <Input placeholder="Enter review title" />
          </Form.Item>
        </Form.Item>
      </Form>
    </Card>
  );
};

export default MessageCard;
