import * as React from "react";
import {
  Row,
  Col,
  Layout,
  Typography,
  Input,
  Form,
  Button,
  Alert,
  Tooltip,
} from "antd";
import MessageCard from "../components/message-card";
import { mockJourneys } from "../mock-data/journeys";
import { IMessage } from "../interfaces/message";
import { InfoCircleTwoTone } from "@ant-design/icons";

const { Sider, Content } = Layout;

const JourneyEditor: React.FC = () => {
  const [highlighted, setHighlighted] = React.useState<string | undefined>(
    undefined
  );

  const [messages, setMessages] = React.useState<IMessage[][]>([]);

  React.useEffect(() => {
    // Group journey messages by previous message id
    const journey = mockJourneys[0];
    const ungroupedMessages: IMessage[] = journey?.messages || [];

    // Create a matrix of messages
    // Each array is a message block grouped by previous message id
    // use reduce
    const groupedMessages = ungroupedMessages.reduce(
      (acc: IMessage[][], message: IMessage) => {
        // try to find array that contains some message with the same previous message id
        const previousMessageId = message.previous_message_id;
        const previousMessageIndex = acc.findIndex(
          (messages: IMessage[]) =>
            messages[0]?.previous_message_id === previousMessageId
        );

        // if found, push the message to the array
        if (previousMessageIndex !== -1) {
          acc[previousMessageIndex].push(message);
        } else {
          // if not found, create a new array and push the message to it
          acc.push([message]);
        }

        return acc;
      },
      // @ts-ignore
      []
    );

    // set messages
    setMessages(groupedMessages);
  }, []);

  const findPreviousMessage = (
    id: string | undefined
  ): IMessage | undefined => {
    // find previous message
    // previous message id is the id of the message that comes before the current message
    // it can be in any message block from messages

    // find message block that contains the message with the given id
    const messageBlock = messages.find((messages: IMessage[]) =>
      messages.find((message: IMessage) => message._id === id)
    );

    // if found, find the previous message in the message block
    if (messageBlock) {
      return messageBlock.find((message: IMessage) => message._id === id);
    }
  };

  return (
    <Layout style={{ height: "100vh", overflow: "hidden" }}>
      <Layout>
        <Alert
          showIcon={false}
          message={
            <>
              <Typography.Text type="secondary">
                To use user's response from one message in another, copy message
                id from the top of the card and paste it inside the bot's
                message text between curly braces.
                <br />
                Input:{" "}
                <Typography.Text italic={true} type="secondary">
                  You said it before {"{"}9976577c77c7ee5476f3ff4a{"}"}.
                </Typography.Text>
                <br />
                Output:{" "}
                <Typography.Text italic={true} type="secondary">
                  You said it before{" "}
                  <Typography.Text type="secondary" strong>
                    Hello Voz
                  </Typography.Text>
                  .
                </Typography.Text>
              </Typography.Text>
            </>
          }
          closable={true}
          type="info"
          banner={true}
        />
        <div
          style={{
            position: "absolute",
            top: 0,
            right: 0,
          }}
        >
          <Tooltip title="hello" placement="bottomLeft">
            <InfoCircleTwoTone size={48} twoToneColor={"#1890ff"} />
          </Tooltip>
        </div>
        <Content style={{ overflow: "auto", padding: 24 }}>
          <Row gutter={16} wrap={false}>
            {messages.map((messageBlock: IMessage[], index: number) => (
              <Col key={index}>
                {messageBlock.map((message: IMessage, index: number) => (
                  <MessageCard
                    key={index}
                    message={message}
                    previousMessage={findPreviousMessage(
                      message.previous_message_id
                    )}
                    highlighted={highlighted}
                    setHighlighted={() => setHighlighted(message._id)}
                  />
                ))}
              </Col>
            ))}
          </Row>
        </Content>
      </Layout>

      <Sider theme="light" style={{ overflow: "auto" }}>
        <div style={{ padding: 16 }}>
          {/* status */}
          <Typography.Text
            type="secondary"
            style={{
              fontSize: "0.8em",
            }}
          >
            Up to date!
          </Typography.Text>

          <Typography.Title level={5}>Basic info</Typography.Title>
          <Form
            layout="vertical"
            initialValues={{
              name: mockJourneys[0].name,
              description: mockJourneys[0].description,
            }}
          >
            <Form.Item label="Name" name="name">
              <Input placeholder="Enter journey name" />
            </Form.Item>

            <Form.Item label="Description" name="description">
              <Input.TextArea
                placeholder="Enter journey description"
                autoSize={{ minRows: 2, maxRows: 6 }}
              />
            </Form.Item>

            <Form.Item>
              <Button block type="primary">
                Save
              </Button>
            </Form.Item>
          </Form>
        </div>
      </Sider>
    </Layout>
  );
};

export default JourneyEditor;
