// Imports
import * as React from "react";
import { Row, Col, Card, PageHeader } from "antd";
import { mockJourneys } from "../mock-data/journeys";
import { IJourney } from "../interfaces/journey";
import { useNavigate } from "react-router-dom";

const JourneyList: React.FC = () => {
  const navigate = useNavigate();

  return (
    <div
      style={{
        margin: 24,
      }}
    >
      <PageHeader title="Journeys" subTitle="List of all journeys" />

      <Row gutter={16}>
        {mockJourneys.map((journey: IJourney) => (
          <Col span={8} key={journey._id}>
            <Card
              title={journey.name}
              hoverable
              onClick={() => navigate(journey._id)}
            >
              <p>{journey.description}</p>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default JourneyList;
